import React, { useState } from "react";
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import AddIcon from '@mui/icons-material/Add';
import BorderColorIcon from '@mui/icons-material/BorderColor';
const Componrnet =()=>{
    const [searchVal,setSerchVal]=useState("");
    const [submitVal,setSubmit]=useState([]);
    const [updateBtn,setUpdate]=useState(true)
    const [editItems,seteditItem]=useState(null)
    const serchValue=(event)=>{
        setSerchVal(event.target.value);
    }
    const submiValue=()=>{
        if(!searchVal){
            alert("Enter the value")

        }else if(searchVal && !updateBtn){
            setSubmit(
                submitVal.map((elm)=>{
                          if(elm===editItems){
                              return{...elm, name:searchVal}
                          }
                          return elm;
                })
                

            )
            
            setUpdate(true);
            setSerchVal(" ");
            seteditItem(null);
            console.log(searchVal)
        }
        
        else{
            const inputData= {id: new Date().getTime().toString(), name:searchVal}
            setSubmit([...submitVal,inputData]);
            setSerchVal(" ");
        }
       
    }
    // delete one by one
    const delitem=(index)=>{
            const upadate=submitVal.filter((value)=>{
                return index != value.id
            })
            setSubmit(upadate)
    }
    const removeALl=()=>{
        setSubmit([]);
    }
    // update item
    const editItem=(id)=>{
            let updateItem=submitVal.find((elem)=>{
                return elem.id===id
            });
            console.log(updateItem);
            setUpdate(false);
            setSerchVal(updateItem.name);
            seteditItem(id)
    }
    return(
        <>
        <section className="main">

        
          <div className="container">
                <div className="row justify-content-center pt-5 align-items-center">
                <div className="col-md-4">
                       <div className="box">
                       <h1>Medical To Do List</h1>
                            <input type="text" placeholder="Enter the Product"  value={searchVal} onChange={serchValue} />
                            {
                                updateBtn ?<AddIcon onClick={submiValue}/> : <BorderColorIcon onClick={submiValue}/>
                            }
                            
                          
                       {
                        submitVal.map((value)=>{
                            return(
                               
                                <div className="d-flex m-4 justify-content-space align-items-center productView flex-xl-row" key={value.id}>
                                <div className="text-left">{value.name} {console.log(value.name)}</div>

                                <div><BorderColorIcon onClick={()=>editItem(value.id)}/><DeleteForeverIcon onClick={()=>delitem(value.id)}/></div>
                            </div>
                            
                        )
                        })
                       }
                      
                       <button type="submit" data-sm-link-text="Remove All" onClick={removeALl} className="btn btn-success">Remove All</button>
                       </div>
                </div>
                </div>
          </div>   
          </section>
        </>
    )
    };
export default Componrnet;
